.ask-section {
  max-width: 542px;
  margin: 0 auto;
  text-align: center;
  padding: 130px 0 0;
}

@media (max-width: 1144px) {
  .ask-section {
    padding: 60px 20px 0;
    box-sizing: border-box;
  }
}

.ask-section .heading {
  text-align: center;
  max-width: 490px;
  margin: 0 auto;
}

.ask-section .heading h2 {
  font-size: 32px;
}

.ask-section .heading p {
  line-height: 1.5;
}

.ask-section ul.accordion-list {
  position: relative;
  display: block;
  width: 100%;
  height: auto;
  padding: 0;
  margin: 58px 0;
  list-style: none;
}

.ask-section ul.accordion-list li {
  position: relative;
  display: block;
  width: 100%;
  height: auto;
  background-color: #FFF;
  padding: 23px 0;
  border-top: 1px solid rgba(145, 148, 161, 0.6);
  cursor: pointer;
}

.ask-section ul.accordion-list li:last-child {
  border-bottom: 1px solid rgba(145, 148, 161, 0.4);
}

.ask-section ul.accordion-list li.active h3:after {
  transform: rotate(180deg);
  color: #fa5757;
}

.ask-section ul.accordion-list li h3 {
  font-weight: 400;
  position: relative;
  display: block;
  width: 100%;
  height: auto;
  padding: 0 0 0 0;
  margin: 0;
  letter-spacing: 0.01em;
  cursor: pointer;
  text-align: left;
  font-size: 18px;
  color: #252b46;
  transition: all 0.5s ease;
}

@media (max-width: 767px) {
  .ask-section ul.accordion-list li h3 {
    font-size: 12px;
  }
}

.ask-section ul.accordion-list li h3:hover {
  color: #fa5757;
}

.ask-section ul.accordion-list li h3:after {
  content: "\e901";
  font-family: 'icomoon' !important;
  position: absolute;
  right: 20px;
  top: -4px;
  color: #5368df;
  transition: all 0.3s ease-in-out;
  font-size: 27px;
  font-weight: bold;
}

@media (max-width: 767px) {
  .ask-section ul.accordion-list li h3:after {
    font-size: 16px;
  }
}

.ask-section ul.accordion-list li div.answer {
  position: relative;
  display: block;
  width: 100%;
  height: auto;
  margin: 0;
  padding: 0;
  cursor: pointer;
}

.ask-section ul.accordion-list li div.answer p {
  position: relative;
  display: block;
  font-weight: 300;
  padding: 20px 0 0 0;
  cursor: pointer;
  margin: 0 0 15px 0;
  font-size: 14px;
  text-align: left;
  line-height: 2;
}

.form-section {
  background: #5368df;
  text-align: center;
  margin-top: 150px;
  padding: 67px 0;
}

@media (max-width: 1144px) {
  .form-section {
    margin-top: 60px;
  }
}

.form-section span {
  letter-spacing: 4.7px;
  font-size: 13px;
  color: #fff;
}

.form-section h4 {
  font-size: 32px;
