  margin: 40px auto;
  color: #fff;
  font-weight: 500;
}

@media (max-width: 767px) {
  .form-section h4 {
    font-size: 22px;
  }
}

.form-section .form {
  display: flex;
  justify-content: center;
}

@media (max-width: 767px) {
  .form-section .form {
    flex-direction: column;
    padding: 0 20px;
    box-sizing: border-box;
  }
}

.form-section .input-form {
  max-width: 300px;
  width: 100%;
  margin-right: 14px;
  box-sizing: border-box;
  border: 2px solid transparent;
}

@media (max-width: 767px) {
  .form-section .input-form {
    max-width: 100%;
  }
}

.form-section .input-form input {
  width: 100%;
  height: 48px;
  padding: 0 21px;
  border-radius: 5px;
  outline: 0;
  border: none;
  box-sizing: border-box;
}

