  font-size: 14px;
  color: #9194a1;
}

.form-section button {
  padding: 15px 22px;
}

.form-section .invalid-input {
  border-color: #fa5757;
  position: relative;
  transition: all 0.5s ease;
  border-radius: 6px;
}

.form-section .invalid-input:before {
  content: url("../images/icon-error.svg");
  position: absolute;
  right: 18px;
  top: 50%;
  transform: translateY(-50%);
}

.form-section .invalid-input:after {
  content: "Whoops, make sure it's an email";
  position: absolute;
  bottom: -25px;
  background: #fa5757;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  left: 0;
  width: 100%;
  color: #fff;
  text-align: left;
  font-weight: 500;
  font-size: 12px;
  padding: 5px 0 5px 10px;
  font-style: italic;
  box-sizing: border-box;
}

@media (max-width: 767px) {
  .form-section .invalid-input:after {
    max-width: 100%;
    box-sizing: border-box;
  }
