.toTop {
  width: 30px;
  height: 30px;
  background: #5368df;
  text-align: center;
  padding: 5px;
  position: fixed;
  bottom: 20px;
  /* отступ кнопки от нижнего края страницы*/
  right: 20px;
  cursor: pointer;
  display: none;
  color: #333;
  font-family: verdana;
  font-size: 18px;
  line-height: 35px;
  border: 2px solid #fff;
  border-radius: 5px;
}

.toTop:before {
  content: "\e901";
  font-family: 'icomoon' !important;
  position: absolute;
  font-size: 25px;
  color: #fff;
  font-weight: 500;
  display: block;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) rotate(180deg);
}
